import React, { useState } from 'react';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import YardOutlinedIcon from '@mui/icons-material/YardOutlined';
import Privacy from '../Renderer/Privacy';
import Popover from '@mui/material/Popover';
import TextField from '@mui/material/TextField';

let name;
let nft;



const regex = /^[a-z|A-Z]+$/;

function MypagePresenter(props) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [mouseX, setMouseX] = useState(0);
    const [mouseY, setMouseY] = useState(0);
    const [createName, setCreateName] = React.useState(true);

    const handleClick = (event) => {
        setMouseX(event.clientX);
        setMouseY(event.clientY);
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;

    function inputName(e) {

        name = e.target.value;

            setCreateName(!regex.test(name))

            if(name.length > 12)
            {
                setCreateName(true)
            }
    }

    function setName() {

        props.user.user_NFT = nft
        props.createSeed(name)
    }

    function nftSave(_nft) {
        nft = _nft
    }

    const createTitleButton = () => {
        const result = [];

        for (let i = 0; i < props.user.user_NFTArray.length; i++) {

            result.push(

                <Button
                    sx={{ width: '18rem', margin: '0.8rem' }}
                    variant="contained" size='large'
                    onClick={() => { handleClick(event), nftSave(props.user.user_NFTArray[i]) }}
                    key={i}>{props.user.user_NFTArray[i].title}
                </Button>
            );
        }

        return result;
    }

    return (


        <Box
            sx={{
                width: '100vw',
                height: '100vh'
            }}>
            <Grid >
                <AppBar position="static"  >
                    <Toolbar variant="dense">
                        <YardOutlinedIcon fontSize="large" />
                        <Typography variant="h6" align='center' sx={{ flexGrow: 1 }} >
                           SEED NFT LIST
                        </Typography>
                        <Privacy {...props} />
                    </Toolbar>
                </AppBar>
                <Grid container
                    direction="column"
                    justifyContent="flex-end"
                    alignItems="center"
                    marginTop="1rem"
                >
                    {createTitleButton()}
                    <div>
                        <Button variant="outlined" aria-describedby={id} onClick={handleClick}
                            disabled={!props.user.user_NFT}
                            sx={{ pt: '6px', pb: '6px', marginTop: '-30px' }}>
                            EDIT NAME
                        </Button>
                        <Popover
                            id={id}
                            open={open}
                            anchorEl={anchorEl}
                            onClose={handleClose}
                        //     anchorReference="anchorPosition"
                        //    anchorPosition={{  left: mouseY, top: mouseY }}
                            anchorOrigin={{ vertical: 'center', horizontal: 'center', }}
                            transformOrigin={{ vertical: 'center', horizontal: 'center', }}
                        >
                            <Box
                              //  component="form"
                                display="flex"
                            >


{
                                    createName ?
                                    <TextField sx={{ 
                                        mt:'1rem' ,mb:'1rem', ml:'1rem'}}
                                    error
                                    id="outlined-error-helper-text" label="Create NFT Name" 
                                    variant="outlined" onChange={inputName}        
                                    helperText="
                                    12 chars or less in English only"
                                  /> : 
                                    <TextField sx={{ 
                                        mt:'1rem' ,mb:'1rem', ml:'1rem'}}
                                    id="outlined-basic" label="Create NFT Name" 
                                    variant="outlined" onChange={inputName}             
                                    helperText="
                                    12 chars or less in English only"
                                  /> 
}


                                <Button variant="contained" 
                                                    disabled = {createName}
                                onClick={() => { setName(); handleClose(); }} 
                                sx={{ margin:'1rem' }}>
                                    SET
                                </Button>
                            </Box>
                        </Popover>
                    </div>
                </Grid>
            </Grid>
        </Box>
    );
}

export default MypagePresenter;
