import MypagePresenter from './MypagePresenter';
import { useNavigate } from 'react-router-dom';


function MypageContainer({ user, setUser }) {
    const navigate = useNavigate();

    const props = {
        user,
        setUser,
        onClickTitleButton: (nft) => {
            setUser((prevState) => {
                return { ...prevState, user_NFT: nft }
            });
        },
        createSeed: (name) => {
            setUser((prevState) => {
                return { ...prevState, user_NFTName: name }
            });
            navigate(`/renderer`);
        },
    }

    return (
        <MypagePresenter {...props} />
    );
}

export default MypageContainer;
