import React, { useState } from 'react';
import MainPresenter from './MainPresenter';
import { useWeb3React } from "@web3-react/core";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
import { Alchemy, Network } from "alchemy-sdk";
import { useNavigate } from 'react-router-dom';

const api = "0caFBF128JudQbQ1Km-zn82t_L7C1Qvw"
const findStr = "monday ape"

let arr = []

const walletconnect = new WalletConnectConnector({
    rpc: {
        1: "https://mainnet.infura.io/v3/00ca1859789d4b40bce01f4104844224",
        56: "https://bsc-dataseed.binance.org/"
    },
    network: "binance",
    qrcode: true,
    pollingInterval: 12000
});

function MainContainer({ user, setUser }) {
    const {
        active,
        account,
        activate,
        deactivate
    } = useWeb3React();

    const navigate = useNavigate();

    const [active_Test, setActive_Test] = useState();
    const [account_Test, setAccount_Test] = useState();

    const props = {
        active,
        account,
        active_Test,
        account_Test,
        user,

        onChangeName: (e) => {
            console.log(e.target.value)
            setName(e.target.value);
        },
        connectWalletConnect: async () => {
            try {
                await activate(walletconnect);
            } catch (ex) {
                console.log(ex)
            }
        },

        walletSearch: async () => {
            if (!active) {
                alert("Not Connect")
                return
            }

            const config = {
                apiKey: api,
                network: Network.ETH_MAINNET,
            }

            const alchemy = new Alchemy(config);

            const getMyNFTs = async (result, pageKey) => {
                result = result ?? []

                const data = await alchemy.nft.getNftsForOwner(account, {
                    pageKey,
                    withMetadata: false
                })

                result = result.concat(data.ownedNfts)

                let nftList = data["ownedNfts"]

                let i = 1

                for (let nft of nftList) {
                    i++
                    if (`${nft.title}`.indexOf(findStr) != -1) {
                        arr.push(nft)
                    }
                }

                if (data.pageKey) {
                    result = await getMyNFTs(result, data.pageKey)
                }

                return result
            }

            arr = [];
            await getMyNFTs()
            props.user.user_Active = active
            props.user.user_Account = account
            props.user.user_NFTArray = arr
            navigate(`/mypage`);
        },

        disconnect: async () => {
                deactivate();
                setActive(false)
                setAccount(null)
                props.user.user_Active = null
                props.user.user_Account = null
                props.user.user_NFTArray = null
        },

        connectWalletConnect_Test: async () => {
            setActive_Test(true)
            setAccount_Test("0xdE9B4206B1499e56E4417f8EdB7bE4586FeD30Ba")
            setUser((prevState) => {
                return {
                    ...prevState,
                    user_Active: true,
                    user_Account: "0xdE9B4206B1499e56E4417f8EdB7bE4586FeD30Ba",
                }
            });
        },

        walletSearch_Test: async () => {
            if (!active_Test) {
                alert("Not Connect")
                return
            }

            const config = {
                apiKey: api,
                network: Network.ETH_MAINNET,
            }

            const alchemy = new Alchemy(config);

            const getMyNFTs = async (result, pageKey) => {
                result = result ?? []

                const data = await alchemy.nft.getNftsForOwner(account_Test, {
                    pageKey,
                    withMetadata: false
                })

                result = result.concat(data.ownedNfts)

                let nftList = data["ownedNfts"]

                let i = 1

                for (let nft of nftList) {
                    i++
                    if (`${nft.title}`.indexOf(findStr) != -1) {
                        arr.push(nft)
                    }
                }

                if (data.pageKey) {
                    result = await getMyNFTs(result, data.pageKey)
                }

                return result
            }

            arr = [];
            await getMyNFTs()
            props.user.user_Active = active_Test
            props.user.user_Account = account_Test
            props.user.user_NFTArray = arr
            navigate(`/mypage`)
        },

        disconnect_Test: async () => {
            setActive_Test(false)
            setAccount_Test(null)
            props.user.user_Active = null
            props.user.user_Account = null
            props.user.user_NFTArray = null
        }
    }

    return (
        <MainPresenter {...props} />
    );
}

export default MainContainer