import React, { useState } from "react";
import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import MainScreen from './screens/Main';
import MypageScreen from './screens/Mypage';
import RendererScreen from './screens/Renderer'
import './App.css'

const theme = createTheme({
  typography: {
    "fontFamily": `"Raleway", sans-serif`,
    "fontSize": 14,
    "fontWeightLight": 300,
    "fontWeightRegular": 400,
    "fontWeightMedium": 500
  },
});

const JE_Position = {
  lat: 33.396514, lng: 126.344930
};

const YO_Position = {
  lat: 34.750594, lng: 127.745762
};

const GA_Position = {
  lat: 37.791855, lng: 128.90720,
};

function App() {
  let [user, setUser] = useState({
    user_Active: null,
    user_Account: null,
    user_NFT: null,
    user_NFTArray: null,
    user_NFTName: null,
    user_NFTComing: null,
    user_NFTNow: null,
    user_NFTEnded: null,
    user_ShowRoomEndedArr: [],
  });

  function showRoom() {
    let name, position, dateStart, dateEnd;
  }

  function new_ShowRoom(_name, _position, _dateStart, _dateEnd) {
    let room = new showRoom();
    room.name = _name;
    room.position = _position;
    room.dateStart = _dateStart;
    room.dateEnd = _dateEnd;
    return room;
  }

  function addComingArr(_name, _position, _dateStart, _dateEnd) {
    let room = new_ShowRoom(_name, _position, _dateStart, _dateEnd);
    user.user_NFTComing = room;
  }

  function addNowArr(_name, _position, _dateStart, _dateEnd) {
    let room = new_ShowRoom(_name, _position, _dateStart, _dateEnd);
    user.user_NFTNow = room;
  }

  function addEndedArr(_name, _position, _dateStart, _dateEnd) {
    let room = new_ShowRoom(_name, _position, _dateStart, _dateEnd);
    user.user_NFTEnded = room;
    user.user_ShowRoomEndedArr.push(room)
  }

  function removeComing() {
    user.user_NFTComing = null;
  }

  function removeNow() {
    user.user_NFTNow = null;
  }

  function removeEnded() {
    user.user_NFTEnded = null;
  }

  function removeEndedArr() {
    user.user_ShowRoomEndedArr = null;
  }

  function removeAll() {
    user.user_NFTComing = null;
    user.user_NFTNow = null;
    user.user_NFTEnded = null;
    user.user_ShowRoomEndedArr = null;
  }

  function AddShowRoom() {
    addComingArr('', JE_Position, "2022.11.20", "2030.05.20");
    addNowArr('', YO_Position, "2023.11.21", "2031.11.20");
    addEndedArr('1', GA_Position, "2021.11.22", "2031.11.20");
    addEndedArr('2', GA_Position, "2022.11.22", "2032.11.20");
    addEndedArr('3', GA_Position, "2022.11.22", "2032.11.20");
    addEndedArr('4', GA_Position, "2022.11.22", "2032.11.20");
    addEndedArr('5', GA_Position, "2022.11.22", "2032.11.20");
    addEndedArr('6', GA_Position, "2022.11.22", "2032.11.20");
    addEndedArr('7', GA_Position, "2022.11.22", "2032.11.20");
    addEndedArr('8', GA_Position, "2022.11.22", "2032.11.20");
  }

  return (
    <>
      <ThemeProvider theme={theme}>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <Routes>
          <Route path="/main" element={<MainScreen user={user} setUser={setUser} />} />
            <Route path="/" element={<Navigate replace to="/main" />} />
            <Route path="/mypage" element={<MypageScreen user={user} setUser={setUser} />} />
            <Route path="/renderer" element={<RendererScreen user={user} setUser={setUser} AddShowRoom={AddShowRoom} />} />
          </Routes>
        </BrowserRouter>
      </ThemeProvider>
    </>
  )
}

export default App;
