
import React, { useCallback, useEffect, useState } from 'react';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import IconButton from '@mui/material/IconButton';
import { Button } from '@mui/material';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import PropTypes from 'prop-types';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import Popover from '@mui/material/Popover';
import TextField from '@mui/material/TextField';

let name ;

const regex = /^[a-z|A-Z]+$/;

const emails = ['username@gmail.com', 'user02@gmail.com'];

//let createName = true;
function SimpleDialog(props) {
    const { onClose, selectedValue, open } = props;
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [createName, setCreateName] = React.useState(true);

    const handleClose = () => {
        onClose(selectedValue);
    };
    
    //
    const phandleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const phandleClose = () => {
        setAnchorEl(null);
    };


    const popen = Boolean(anchorEl);
    const id = popen ? 'simple-popover' : undefined;


    
    function inputName(e) {


            name = e.target.value;

            setCreateName(!regex.test(name))

            if(name.length > 12)
            {
                setCreateName(true)
            }
    }
    function setName() {
        console.log(name)
        console.log(props)


        props.setNickName(name)

    }

    return (


        <Dialog onClose={handleClose} open={open} >
            <DialogTitle fontSize={26}  textAlign='center' 
                sx={{ pt: '16px', pb: '40px' , width:'180px'}}>PRIVACY</DialogTitle>
            <Grid container
                direction="column"
                alignItems="center"
                justifyContent="center"
            >
                <div>
                    <Button variant="outlined" 
                    aria-describedby={id} onClick={phandleClick}
                    disabled = {!props.user.user_NFT}
                        sx={{ pt: '6px', pb: '6px', marginTop:'-30px' }}>

                        EDIT NAME
                    </Button>
                    <Popover
                        id={id}
                        open={popen}
                        anchorEl={anchorEl}
                        onClose={phandleClose}
                        anchorOrigin={{
                            vertical: 'center',
                            horizontal: 'center',
                        }}
                    >
                        <Box
                            component="form"
                           display="flex"
                        >
                                {
                                    createName ?
                                    <TextField sx={{ 
                                        mt:'1rem' ,mb:'1rem', ml:'1rem'}}
                                    error
                                    id="outlined-error-helper-text" label="Change NFT Name" 
                                    variant="outlined" onChange={inputName}        
                                    helperText="
                                    12 chars or less in English only"
                                  /> : 
                                  <TextField sx={{ 
                                    mt:'1rem' ,mb:'1rem', ml:'1rem'}}
                                  id="outlined-basic" label="Change NFT Name" 
                                  variant="outlined" onChange={inputName}        
                                  helperText="
                                    12 chars or less in English only"
                                /> 
                                }





                            <Button variant="contained" 
                            disabled = {createName}
                            onClick={() => 
                                { setName(); phandleClose(); }} sx={{ m: 1 }}>

                                SET
                            </Button>
                        </Box>
                    </Popover>

                </div>
            </Grid>

            <List >



                <ListItem >



                </ListItem>

                <ListItem sx={{ pt: '0px', pb: '0px' }}>

                    <ListItemText primary="NFT-NAME"
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFTName ? props.user.user_NFTName : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />

                </ListItem>


                <ListItem >
                    <ListItemText primary={"NFT-TITLE"}
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFT ? props.user.user_NFT.title : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />
                </ListItem>
                <ListItem >
                    <ListItemText primary={"NFT-TOKEN-ID"}
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFT ? props.user.user_NFT.tokenId : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />
                </ListItem>
                <ListItem >
                    <ListItemText primary={"NFT-TOKEN-TYPE"}
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFT ? props.user.user_NFT.tokenType : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />
                </ListItem>


                <ListItem>

                    <ListItemText primary="ACCOUNT"
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_Active ? props.user.user_Account.substr(0, 6) + "..." : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />

                </ListItem>
            </List>


        </Dialog>

    );
}











SimpleDialog.propTypes = {
    onClose: PropTypes.func.isRequired,
    open: PropTypes.bool.isRequired,
    selectedValue: PropTypes.string.isRequired,
};


function Privacy(props) {
    const [open, setOpen] = React.useState(false);
    const [selectedValue, setSelectedValue] = React.useState(emails[1]);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = (value) => {
        setOpen(false);
        setSelectedValue(value);
    };


    function showPrivacy() {
        console.log(props.account_Test);
    }



    return (
        <>
            <IconButton
                edge="end"
                color="inherit" onClick={handleClickOpen}
>
                <AccountCircleIcon fontSize="large" />
            </IconButton>
            <SimpleDialog {...props}
                selectedValue={selectedValue}
                open={open}
                onClose={handleClose}
            />

        </>
    );
}

export default Privacy;