import React, { useEffect, useState } from 'react';
import { GoogleMap, useJsApiLoader, Marker } from '@react-google-maps/api';
import { Button } from '@mui/material';
import { styled } from '@mui/material/styles';
import MarkerImg from './pin.png';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import RemoveCircleTwoToneIcon from '@mui/icons-material/RemoveCircleTwoTone';
import AddCircleTwoToneIcon from '@mui/icons-material/AddCircleTwoTone';
import IconButton from '@mui/material/IconButton';

let curZoom = 3;
let ShowRoomEndedArr = [];

let coming, currerent, ended
let none = null

function showRoom() {
  let name, position, dateStart, dateEnd;
}

function new_ShowRoom(_name, _position, _dateStart, _dateEnd) {
  let room = new showRoom();
  room.name = _name;
  room.position = _position;
  room.dateStart = _dateStart;
  room.dateEnd = _dateEnd;
  return room;
}

const NONE_Position = {
  lat: 36.594678, lng: 127.985756
}

let firstShowRoom = false;
let names = []
const containerStyle = {
  width: '100vw',
  height: '100vh',
};



function pick() {
  if (currerent) {
    return currerent;
  }
  else if (ended) {
    return ended;
  }
  else if (coming) {
    return coming;
  }
  else
    return none = new_ShowRoom("NONE", NONE_Position, "0", "0");
}

function GoogleMapPresenter(props) {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: "AIzaSyDAR5IsWK28BMFmf7TBPcToCG7sQoO81aA"
  })

  coming = props.user.user_NFTComing
  currerent = props.user.user_NFTNow
  ended = props.user.user_NFTEnded


  names = []
  ShowRoomEndedArr = []
  ShowRoomEndedArr = props.user.user_ShowRoomEndedArr

  for (let i = 0; i < ShowRoomEndedArr.length; ++i) {
    let a = ShowRoomEndedArr[i].dateStart + " ~ " + ShowRoomEndedArr[i].dateEnd + " - " + ShowRoomEndedArr[i].name
    names.unshift(a)
  }

  if (!coming && !currerent && !ended) {
    firstShowRoom = false;
  }
  else {
    firstShowRoom = true;
  }

  const [map, setMap] = React.useState(null)

  const onLoad = React.useCallback(function callback(map) {
    loadMap();
    const bounds = new window.google.maps.LatLngBounds(pick() ? pick().position : null);
    map.fitBounds(bounds);

    setMap(map)
  }, [])

  const onUnmount = React.useCallback(function callback(map) {
    setMap(map)
  }, [])

  const onLoad2 = marker => {
    console.log('marker: ', marker)
  }

  async function loadMap() {
    bhandleToggle()

    setTimeout(() => {
      bhandleClose()
    }, 600);
  }

  const [zoom, setZoom] = useState()

  useEffect(() => {
    setTimeout(() => {
      setZoom(3)
    }, 800);
  }, [])

  const MarkerClick = (p) => {
    loadMap()
    map.setCenter(p);
    setZoom(curZoom)
    setTimeout(() => {
      setZoom(15)
    }, 400);

  }

  var myOptions = {
    panControl: false,
    zoomControl: false,
    scaleControl: false,
    streetViewControl: false,
    mapTypeControl: false,
    overviewMapControl: false,
    rotateControl: false,
    disableDefaultUI: true,
    clickableIcons: false,
  }

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen((prev) => !prev);
  };

  const handleClickAway = () => {
    setOpen(false);
  };

  const [personName, setPersonName] = React.useState([]);
  const handleChangeMultiple = (event) => {
    const { options } = event.target;
    const value = [];
    for (let i = 0, l = options.length; i < l; i += 1) {
      if (options[i].selected) {
        value.push(options[i].value);
      }
    }
    setPersonName(value);
  };


  const [bopen, setbOpen] = React.useState(false);
  const bhandleClose = () => {
    setbOpen(false);
  };
  const bhandleToggle = () => {
    setbOpen(!open);
  };

  function PlusZoom() {

    let a = curZoom + 1;
    setZoom(a)
  }
  function SuntractZoom() {
    let a = curZoom - 1;
    setZoom(a)
  }

  function handleZoomChanged() {
    console.log(this.getZoom())
    curZoom = this.getZoom()
  }

  return isLoaded ? (
    <>
      <div>
        <Backdrop
          sx={{ color: '#fff', height: '100vh', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={bopen}
        // onClick={bhandleClose}
        >
          <CircularProgress color="inherit" >

          </CircularProgress>
        </Backdrop>
      </div>


      <Box
      >
        <Grid
          sx={{ height: '6vh ' }}
        >
          <GoogleMap
            onZoomChanged={handleZoomChanged}
            mapContainerStyle={containerStyle}

            onLoad={onLoad}
            onUnmount={onUnmount}
            zoom={zoom}
            options={myOptions}
            center={pick().position}

          >
            {
              !firstShowRoom ?

                <Marker
                  optimized={false}
                  zIndex={5}
                  label={{ src: { MarkerImg }, text: "Have no plans", color: '#000', fontSize: "20px", fontWeight: "500" }}
                  onLoad={onLoad2}

                  position={currerent ? currerent.position : NONE_Position}

                  icon={{
                    url: MarkerImg,

                    anchor: new google.maps.Point(90, 60),
                    scaledSize: new google.maps.Size(180, 180)
                  }}
                />


                : null
            }
            {
              currerent ?
                <Marker

                  optimized={false}
                  zIndex={5}
                  label={{
                    src: { MarkerImg }, text: currerent ? "NOW" : "cNONE",
                    color: '#000', fontSize: "12px", fontWeight: "600"
                  }}
                  onLoad={onLoad2}

                  position={currerent ? currerent.position : NONE_Position}
                  onClick={() => MarkerClick(currerent.position)}
                  icon={{
                    url: MarkerImg,

                    anchor: new google.maps.Point(30, 40),
                    scaledSize: new google.maps.Size(60, 120)
                  }}
                /> : null
            }


            {
              coming ?
                <Marker
                  optimized={false}
                  zIndex={4}
                  label={{
                    src: { MarkerImg }, text: coming ? "COMING" : "cNONE", color: '#000',
                    fontSize: "12px", fontWeight: "600"
                  }}
                  onLoad={onLoad2}
                  position={coming ? coming.position : NONE_Position}

                  onClick={() => MarkerClick(coming.position)}
                  icon={{
                    url: MarkerImg,

                    anchor: new google.maps.Point(40, 40),
                    scaledSize: new google.maps.Size(80, 120)
                  }}
                /> : null
            }


            {
              ended ?
                <Marker
                  optimized={false}
                  zIndex={5}
                  label={{ src: { MarkerImg }, text: ended ? "ENDED" : "eNONE", color: '#000', fontSize: "12px", fontWeight: "600" }}
                  onLoad={onLoad2}
                  position={ended ? ended.position : NONE_Position}
                  onClick={() => MarkerClick(ended.position)}

                  icon={{
                    url: MarkerImg,


                    anchor: new google.maps.Point(30, 38),
                    scaledSize: new google.maps.Size(60, 120)
                  }}
                /> : null
            }

          </GoogleMap>

        </Grid>
        <>
          <Grid container
            direction="row"
            alignItems="center"
            justifyContent="center"
          >


            <Grid >
              <Button variant="contained"
                sx={{ padding: '4%' }}
                style={{
                  marginTop: "40%", width: "90px",
                  display: "grid", borderRadius: 8,
                }}
                onClick={() => coming ? MarkerClick(coming.position) : null} >
                <Typography
                  fontSize="10px">
                  COMING
                </Typography>
                <Typography
                  sx={{ marginTop: '-10%' }}
                  fontSize="10px" fontWeight={"100"}>
                  {coming ? coming.name : "NONE"}
                </Typography>
              </Button>
            </Grid>

            <Grid item mr={'2vw'} ml={'2vw'} >

            </Grid>

            <Grid item >
              <Button variant="contained"
                sx={{ padding: '4%' }}
                style={{
                  marginTop: "40%", width: "90px",
                  display: "grid", borderRadius: 8,
                }}
                onClick={() => currerent ? MarkerClick(currerent.position) : null} >
                <Typography
                  fontSize="10px">
                  NOW
                </Typography>
                <Typography
                  sx={{ marginTop: '-10%' }}
                  fontSize="10px" fontWeight={"100"}>
                  {currerent ? currerent.name : "NONE"}
                </Typography>
              </Button>


            </Grid>



            <Grid item mr={'2vw'} ml={'2vw'} >


            </Grid>

            <Grid item >
              <Button variant="contained" color="primary"
                sx={{ padding: '4%' }}
                style={{
                  marginTop: "40%", width: "90px",
                  display: "grid", borderRadius: 8,
                }}
                onClick={() => ended ? MarkerClick(ended.position) : null} >
                <Typography
                  fontSize="10px">
                  ENDED
                </Typography>
                <Typography
                  sx={{ marginTop: '-10%' }}
                  fontSize="10px" fontWeight={"100"}>
                  {ended ? ended.name : "NONE"}
                </Typography>

              </Button>
            </Grid>
          </Grid>








          <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"

          >

            <ClickAwayListener onClickAway={handleClickAway} >
              <Box marginTop='0.6rem'>
                <Button onClick={handleClick} variant="contained" color="primary"
                  style={{
                    width: "10rem",



                    borderRadius: 10,

                    fontSize: "10px",

                  }}

                >
                  <Typography
                    fontSize="12px">
                    PAST SHOW LIST
                  </Typography>

                </Button>


                <IconButton
                  color="primary" onClick={PlusZoom}
                  sx={{}}>
                  <AddCircleTwoToneIcon fontSize="large" />
                </IconButton>

                <IconButton
                  color="primary" onClick={SuntractZoom}
                  sx={{}}>
                  <RemoveCircleTwoToneIcon fontSize="large" />
                </IconButton>







                ({none ?
                  <div>
                    {open ? (


                      <Grid item

                        container
                        direction="column"
                        alignItems="center"

                      >
                        <List
                          sx={{
                            width: 'center',
                            height: 'center',
                            position: 'fixed',
                            maxHeight: '10%',
                            bgcolor: 'background.paper',

                            overflow: 'auto',
                            marginTop: '4%',
                            borderRadius: 2,
                            '& ul': { padding: 0 },
                          }}
                        >

                          <ListItem
                          >
                            <Typography
                              fontSize="16px">
                              EMPTY
                            </Typography>
                          </ListItem>


                        </List>
                      </Grid>
                    ) : null}

                  </div>
                  :

                  <div>

                    {open ? (



                      <Grid item

                        container
                        direction="column"
                        alignItems="center"

                      >
                        <List
                          sx={{
                            width: 'center',
                            height: 'center',
                            position: 'fixed',
                            maxHeight: '30%',
                            bgcolor: 'background.paper',

                            overflow: 'auto',
                            marginTop: '4%',
                            borderRadius: 2,
                            '& ul': { padding: 0 },
                          }}
                          subheader={<li />}
                        >
                          {[0].map((sectionId) => (
                            <li key={`section-${sectionId}`}>
                              <ul>
                                {names.map((item) => (
                                  <ListItem key={`${sectionId}-${item}`}
                                  >
                                    <ListItemText primary={` ${item}`}
                                      primaryTypographyProps={{
                                        fontSize: 11,
                                        fontWeight: '400',
                                        textAlign: 'center',
                                        marginBottom: '-7px'
                                      }}

                                    />
                                  </ListItem>
                                ))}
                              </ul>
                            </li>
                          ))}
                        </List>
                      </Grid>
                    ) : null}
                  </div>
                  })


              </Box>

            </ClickAwayListener>

          </Grid>
        </>
      </Box>
    </>

  ) : <></>
}

export default React.memo(GoogleMapPresenter)


